import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Elements from '../elements/';
import classNames from 'classnames';

class QuickReference extends Component {
  render() {
    let {data,classname} = this.props;
   
    return (
      <div className="card border0imp">
            <div className={classNames('card-header green-background', {[`${classname}`]: false })}>{data.title}</div>
        <div className="card-body customFaults border0imp">
        {
          data.values && data.values.map((item, i) => {
            return(<Elements key={i} item={item} />)
          })
        }
        </div>
    </div>
    )
  }
}
QuickReference.defaultProps = {
  data: {}
};
QuickReference.propTypes = {
  data: PropTypes.object
};
export default QuickReference;