import React, { Component } from 'react';
import KEYSext_view from '../KEYSext_view.txt';
import appConfig from '../../appConfig.json';
import {Link} from 'react-router-dom';
import homejson from '../helperJson/homePage.json';
import navbarButtons from '../helperJson/navbarButtons.json';
let baseRoute=appConfig.baseRoute;
let baseUrl=appConfig.baseUrl;
let faultName,source,routeName;
export default class Navbar extends Component {
    constructor(props) {
        super(props);
        
        this.updateState(props,true);
    }
    componentDidMount() {
        let that = this;
        let currentRoute=window.location.href.split('/').pop();
        $("#search_faults, #search_faults_mob").autocomplete({
            source: function (request, response) {
                var results = $.ui.autocomplete.filter(source, request.term);
                if(results.length==0){
                    results = [
                       {
                           label: 'Not Found'
                       }
                   ];
                   response(results);
               }
               else{
                   response(results.slice(0, 15));
               }
                response(results.slice(0, 15));

            },
            response: function (event, ui) {
            },
            select: function (event, ui) {
                let sel_val=ui.item.value;
                if(sel_val=="Not Found")
				{
					ui.item.value="";
					return;
				}
                if(routeName=="cautions"){
                    sel_val= sel_val.split('-')[0];
                }
                else{
                  sel_val = sel_val.replace(/[ /]+/g, "-");  
                }
				 
                window.location.href=baseUrl+routeName+'/'+sel_val;
                // that.props.history.push({
				// 	pathname: routeName+'/'+sel_val,
				// 	state: { detail: sel_val}
				//   })
            },
        });
        $('#navbar_menu_container li').click(function(){
            let dataId=$(this).data('id');
            if(dataId=="external_view"){
                window.location.href=baseUrl+'/access-panel';
            }
        })
    }
   componentWillReceiveProps(props){
    this.updateState(props,false)
   }

   updateState(props,firstTime){
       let that=this;
    this.homePage = appConfig.homePage;
    let pathname=props.location.pathname;
    let fault_code=pathname.split('/').pop();   

    if(pathname.includes("/cautions")){
       source=homejson.upperpart["caution_lights"]
       faultName="cautions";
        routeName="cautions";
    }
    else if(pathname.includes("/engine-apu-ecs-fault")){
        source=homejson.faults["engine/apu/ecs fault"].faults,
        routeName="engine-apu-ecs-fault";
        faultName="engine-apu-ecs-fault";
    }
    else if(pathname.includes("/display-fault")){
        source=homejson.faults["display fault msg"].faults,
        routeName="display-fault";
        faultName="display-fault";
    }

    let currentFault=navbarButtons[faultName][fault_code];
    if(firstTime){
        that.state={
            button1:currentFault && currentFault.button1?currentFault.button1:'',
            button1Class:currentFault && currentFault.button1Class?currentFault.button1Class:'',
            button2:currentFault && currentFault.button2?currentFault.button2:'',
            button2Class:currentFault && currentFault.button2Class?currentFault.button2Class:'',
            button3:currentFault && currentFault.button3?currentFault.button3:'',
            button3Class:currentFault && currentFault.button3Class?currentFault.button3Class:'',
            button4:currentFault && currentFault.button4?currentFault.button4:'',
            button4Class:currentFault && currentFault.button4Class?currentFault.button4Class:'',
        }
    }
    else{
          that.setState({
        button1:currentFault && currentFault.button1?currentFault.button1:'',
        button1Class:currentFault && currentFault.button1Class?currentFault.button1Class:'',
        button2:currentFault && currentFault.button2?currentFault.button2:'',
        button2Class:currentFault && currentFault.button2Class?currentFault.button2Class:'',
        button3:currentFault && currentFault.button3?currentFault.button3:'',
        button3Class:currentFault && currentFault.button3Class?currentFault.button3Class:'',
        button4:currentFault && currentFault.button4?currentFault.button4:'',
        button4Class:currentFault && currentFault.button4Class?currentFault.button4Class:'',
    })
    }
  
   }
    gotoHomePage(e) {
        e.preventDefault();
      
        window.location.href = baseUrl;
    }
    render() {
        return (
            <React.Fragment> 
                <nav className="nav-pc">
                    <div className="home">
                        <a href={baseUrl} className="home-icon"><i className="fa fa-home" aria-hidden="true"></i></a>
                    </div>
                    <div className="search search-area-container">
                        <form className="form-inline">
                            <input className="form-control mr-sm-2 col-12" id="search_faults" type="search" placeholder="Search" aria-label="Search" />
                        </form>
                    </div>
                    <div className="menu">
                        <ul>
                           {
                           this.state.button1 && <li>
                                <a className={this.state.button1Class} href="#">{this.state.button1}</a>
                            </li>
                            }
                             {
                           this.state.button2 && <li>
                                <a className={this.state.button2Class} href="#">{this.state.button2}</a>
                            </li>
                            }
                             {
                           this.state.button3 && <li>
                                <a className={this.state.button3Class} href="#">{this.state.button3}</a>
                            </li>
                            }
                             {
                           this.state.button4 && <li>
                                <a className={this.state.button4Class} href="#">{this.state.button4}</a>
                            </li>
                            }

                            {
                           !this.state.button1 && <li>
                                <a className="bg-primary" href="#">System</a>
                            </li>
                            }
                             {
                           !this.state.button2 && <li>
                                <a className="bg-danger" href="#">ARCDU </a>
                            </li>
                            }
                            
                            <li>
                                <a href="#"><span aria-hidden="true" >&times;</span></a>
                            </li>
                        </ul>
                    </div>
                </nav>

                <nav className="nav-mobi">
                    <div className="home">
                        <a href={baseUrl} className="home-icon"><i className="fa fa-home" aria-hidden="true"></i></a>
                    </div>
                    <div className="search search-area-container">
                        <form className="form-inline">
                            <input className="form-control mr-sm-2 col-12" id="search_faults_mob" type="search" placeholder="Search" aria-label="Search" />
                        </form>
                    </div>
                    <div className="menu">
                    <div className="hamburger" style={{ backgroundImage: `url(${homejson.icons_folder+'menu.svg'})`}}>

                        {/* <ul>
                            <li className="hamburger">
                                <a type="button" className="hamburger">
                                    <i className="fa fa-bars" aria-hidden="true"></i>
                                </a>
                            </li>
                        </ul> */}
                        </div>
                    </div>
                </nav>
                <div className="click-mobile">
                        <ul>
                        {
                           this.state.button1 && <li>
                                <a className={this.state.button1Class} href="#">{this.state.button1}</a>
                            </li>
                            }
                             {
                           this.state.button2 && <li>
                                <a className={this.state.button2Class} href="#">{this.state.button2}</a>
                            </li>
                            }
                             {
                           this.state.button3 && <li>
                                <a className={this.state.button3Class} href="#">{this.state.button3}</a>
                            </li>
                            }
                             {
                           this.state.button4 && <li>
                                <a className={this.state.button4Class} href="#">{this.state.button4}</a>
                            </li>
                            }

                            {
                           !this.state.button1 && <li>
                                <a className="bg-primary" href="#">System</a>
                            </li>
                            }
                             {
                           !this.state.button2 && <li>
                                <a className="bg-danger" href="#">ARCDU </a>
                            </li>
                            }
                        </ul>
                    </div>

            </React.Fragment>
        )
    }
}


