import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Elements from '../elements/';
import classNames from 'classnames';

class QuickLinks extends Component {
  render() {
    let {data,classname} = this.props;
  
    return (
      <div className="card">
           <div className={classNames('card-header green-background', {[`${classname}`]: false })}>{data.title}</div>
        <div className="card-body left-card customFaults">
        {
          data.values && data.values.map((item, i) => {
            return(<Elements key={i} item={item} />)
          })
        }
        </div>
    </div>
    )
  }
}
QuickLinks.defaultProps = {
  data: {}
};
QuickLinks.propTypes = {
  data: PropTypes.object
};
export default QuickLinks;